body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  text-align: center;
  background-color: #34495e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;  
  align-items: center;
}

.header {
  font-size: 40px;
  color: white;
}

.footer {
  color: white;
  margin-top: -45px;
}

.block { 
  justify-content: center;
  text-align: center;
  margin-top: 30px;
}

.task-block {
  flex-direction: row;
  width: 83vh;
  height: 40vh;
  background-color: white;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}

.task {
  flex-direction: row;
}

.textInput {
  height: 65px;
  width: 400px; 
  padding-left: 40px;
  padding-right: 100px;
  font-size: 20px;
}

.btn {
  color: grey;
  font-size: 40px;
  cursor: pointer;
  margin-top: -65px;
  margin-left: 450px;
}

.btn:hover {
  color: black
}

.tab-bar {
  justify-content: center;
  display: flex;
  flex-direction: row;
  cursor: pointer;
  margin-top: -40px;
  margin-bottom: -40px;
}
.tab {
  margin: 50px;
  padding: 5px;
  border-radius: 10%;
  color: grey;
  background-color: white;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
